<template>
  <b-container fluid="" class="mt--5 position-sticky">
    <b-row>
      <b-col class="col-xl-3 mb-5 mb-xl-0 mw-2">
        <token-info :tokenInfo="tokenInfo" @onClick="changePool" class="bg-light h-100" />
      </b-col>
      <b-col class="col-xl-6 mb-5 mb-xl-0 mw-3">
        <div class="h-100">
          <b-card no-body class="bg-light">
            <k-line-widget
              v-if="!!tokenInfo.symbol"
              :tokenInfo="tokenInfo"
              ref="chart" />
            <div v-if="!tokenInfo.symbol" class="p-3">
              Chart Loading...
            </div>
          </b-card>
          <b-card class="mt-3 bg-light">Transaction here</b-card>
        </div>
      </b-col>
      <b-col class="col-xl-3 mb-5 mb-xl-0 mw-2">
        <b-card class="bg-light h-100" title="Promote Tokens">Promote tokens</b-card>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>

import KLineWidget from '@/components/Chart/KLineWidget'
import { apiGet } from '@/api'
import TokenInfo from '@/components/Chart/TokenInfo'
export default {
  name: 'ChartToken',
  components: { TokenInfo, KLineWidget },
  data () {
    return {
      tokenInfo: {}
    }
  },
  props: ['token'],
  methods: {
    loadTokenInfo: function () {
      apiGet('https://api.thecoin.one/token/info/' + this.token).then((data) => {
        this.tokenInfo = data.token
        console.log(this.tokenInfo)
      })
    },
    changePool: function (pool) {
      this.$refs.chart.setPool(pool)
    }
  },
  created () {
    this.loadTokenInfo()
  },
  mounted () {
    console.log(this.$refs.chart)
  }
}
</script>
